import React from 'react'
import img1 from '../prashnapp.jpg'



const Header = () => {
    return (
        <div>

        <header class="masthead bg-primary text-white text-center">
            <div class="container d-flex align-items-center flex-column">
                <img class="masthead-avatar mb-5" src={img1} alt="" />
                <h1 class="masthead-heading text-uppercase mb-0">Start website</h1>
                <div class="divider-custom divider-light">
                    <div class="divider-custom-line"></div>
                    <div class="divider-custom-icon"><i class="fas fa-star"></i></div>
                    <div class="divider-custom-line"></div>
                </div>
                <p class="masthead-subheading font-weight-light mb-0">Graphic Artist - Web Designer - karishma</p>
            </div>
        </header>
            
        </div>
    )
}

export default Header
